<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
            <el-breadcrumb-item>编辑商品</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card>
            <!-- 提示区域 -->
            <el-alert title="编辑商品信息" type="info" center show-icon :closable="false" />
            <!-- 进度条区域 -->
            <el-steps :active="activeIndex * 1" finish-status="success" align-center>
                <el-step title="基本信息" />
                <el-step title="商品参数" />
                <el-step title="商品属性" />
                <el-step title="商品图片" />
                <el-step title="商品内容" />
                <el-step title="完成" />
            </el-steps>
            <!-- 标签页区域 -->
            <el-form ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="100px" label-position="top">
                <el-tabs tab-position="left" v-model="activeIndex" :before-leave="beforeTabChange" @tab-change="tabChanged">
                    <el-tab-pane label="基本信息" name="0">
                        <el-form-item label="商品名称" prop="goods_name">
                            <el-input v-model="editForm.goods_name" placeholder="请输入商品名称" />
                        </el-form-item>
                        <el-form-item label="商品价格" prop="goods_price">
                            <el-input v-model="editForm.goods_price" type="number" placeholder="请输入商品价格" />
                        </el-form-item>
                        <el-form-item label="商品重量" prop="goods_weight">
                            <el-input v-model="editForm.goods_weight" type="number" placeholder="请输入商品重量" />
                        </el-form-item>
                        <el-form-item label="商品数量" prop="goods_number">
                            <el-input v-model="editForm.goods_number" type="number" placeholder="请输入商品数量" />
                        </el-form-item>
                        <el-form-item label="商品分类">
                            <el-cascader v-model="goods_cat" :options="cateList" :props="cascadeProps" disabled/>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品参数" name="1">
                        <!-- 渲染商品参数的各项数据 -->
                        <el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox v-for="(cb, i) in item.attr_vals" :key="i" :label="cb" border />
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品属性" name="2">
                        <el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
                            <el-input v-model="item.attr_vals"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品图片" name="3">
                        <el-form-item>
                            <el-upload :file-list="uploadFileList" :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove"
                                list-type="picture" :headers="headerObj" :on-success="handleSuccess">
                                <el-button type="primary" size="small">点击上传</el-button>
                            </el-upload>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品内容" name="4">
                        <!-- 富文本编辑器组件 -->
                        <quill-editor v-model:content="editForm.goods_introduce" toolbar="full" content-type="html" @textChange="textChange"></quill-editor>
                        <!-- 编辑商品的按钮 -->
                        <el-button type="primary" class="btnEdit" @click="editGood">编辑商品</el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <!-- 预览图片的对话框 -->
        <el-dialog v-model="previewDialogVisible" title="图片预览" width="60%">
            <img :src="previewPath" alt="预览图片" class="previewImg">
        </el-dialog>
    </div>
</template>

<script>
import { ArrowRight, Search, Edit, Delete, Setting } from '@element-plus/icons-vue'
export default {
    data() {
        const numValidate = (rule, value, callback) => {
            if (value < 0) {
                callback(new Error('输入的数字不能小于0'));
            }
            callback();
        }
        return {
            ArrowRight, Search, Edit, Delete,
            // 步骤条的进度管理
            activeIndex: '0',
            // 编辑商品的表单数据对象
            editForm: {
                goods_name: '',
                goods_price: 0,
                goods_number: 0,
                goods_weight: 0,
                goods_cat: '',
                // 已上传图片的地址数组
                pics: [],
                // 商品的详情描述
                goods_introduce: '',
                // 动态参数和静态属性的数组
                attrs: [],
            },
            // 编辑商品的表单校验对象
            editFormRules: {
                goods_name: [
                    { required: true, message: '请输入商品名称', trigger: 'blur' },
                ],
                goods_price: [
                    { required: true, message: '请输入商品价格', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
                goods_weight: [
                    { required: true, message: '请输入商品重量', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
                goods_number: [
                    { required: true, message: '请输入商品数量', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
            },
            // 商品分类列表
            cateList: [],
            // 商品的分类id字符串
            goods_cat: [],
            // 级联选择器的配置对象
            cascadeProps: {
                expandTrigger: 'hover',
                label: 'cat_name',
                value: 'cat_id',
                children: 'children',
            },
            // 动态参数列表数据
            manyTableData: [],
            // 静态属性列表数据
            onlyTableData: [],
            // 上传图片的路径
            uploadURL: "http://127.0.0.1:8888/api/private/v1/upload",
            // 上传图片的请求头
            headerObj: {
                Authorization: window.sessionStorage.getItem('token')
            },
            // 已经上传的文件列表
            uploadFileList: [],
            // 将要预览的图片的url
            previewPath: '',
            // 控制预览图片对话框的显示与隐藏
            previewDialogVisible: false,
        }
    },
    created() {
        if (!window.sessionStorage.getItem('goodId')) {
            this.$error({message: '无效的商品信息！', duration: 1200});
            this.$router.push('/home/goods');
        }
        this.goodId = window.sessionStorage.getItem('goodId');
        this.getGoodInfo();
        this.getCateList();
    },
    methods: {
        //查询商品信息
        getGoodInfo() {
            this.$api.httpGet(`goods/${this.goodId}`,{showOuter: 'error', error: ['商品信息获取失败！']})
            .then(res => {
                // 从网络响应中获取editForm表单中的数据
                for (let key in this.editForm) {
                    this.editForm[key] = res.data[key];
                }
                // 从网络响应中获取商品的分类信息
                this.goods_cat = res.data.goods_cat.length === 0 ? [] : res.data.goods_cat.split(',');
                this.goods_cat.forEach((item, index) => {
                    this.goods_cat[index] = parseInt(item);
                })

                // 从网络响应中获取动态参数和静态属性
                res.data.attrs.forEach(item => {
                    if (item.attr_sel === 'many') {
                        // 是动态参数的时候
                        this.manyTableData.push({
                            attr_id: item.attr_id, 
                            attr_name: item.attr_name,
                            attr_vals: item.attr_vals.length === 0 ? [] : item.attr_vals.split(','),
                        })
                    } else {
                        // 是静态属性的时候
                        this.onlyTableData.push({
                            attr_id: item.attr_id, 
                            attr_name: item.attr_name,
                            attr_vals: item.attr_vals
                        })
                    }
                })

                // 将服务器中的商品图片拿出来
                res.data.pics.forEach(item => {
                    this.uploadFileList.push({name: '点击预览', url: item.pics_mid_url, pics_id: item.pics_id, saved: true})
                })
            })
        },
        // 获取所有的商品分类列表
        getCateList() {
            this.$api.httpGet('categories', { showOuter: 'error', error: ['获取商品分类信息失败！'] })
                .then(res => {
                    this.cateList = res.data;
                })
        },
        // 标签页被切换后触发
        tabChanged() {
            // 当访问动态参数面板时
            if (this.activeIndex === '1') {
                this.getManyTableData();
            } else if (this.activeIndex === '2') {
                // 当访问静态属性面板时
                this.getOnlyTableData();
            }
        },
        // 获取动态参数
        getManyTableData() {
            this.$api.httpGet(`categories/${this.cateId}/attributes`, { showOuter: 'error', error: ['获取分类参数失败！'] }, 200, { params: { sel: 'many' } })
            .then(res => {
                res.data.forEach(item => {
                    item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',');
                })
                this.manyTableData = res.data;
            })
        },
        // 获取静态属性
        getOnlyTableData() {
            this.$api.httpGet(`categories/${this.cateId}/attributes`, { showOuter: 'error', error: ['获取分类属性失败！'] }, 200, { params: { sel: 'only' } })
            .then(res => {
                this.onlyTableData = res.data;
            })
        },
        // 处理图片预览效果
        handlePreview(file) {
            if (file.saved) {
                // 如果是已经保存到服务器中的图片
                this.previewPath = file.url;
            } else {
                // 如果是已经上传，但为保存到服务器中的图片
                this.previewPath = file.response.data.url;
            }
            this.previewDialogVisible = true;
        },
        // 处理移除图片的操作
        handleRemove(file) {
            if (file.saved) {
                // 如果是已经保存到服务器中的图片
                // 根据临时路径,从 pics 数组中查找到要删除的图片的索引值
                const i = this.editForm.pics.findIndex(item => item.pics_id === file.pics_id)
                // 调用数组的 splice 方法, 把图片信息对象从 pics 数组中移除
                this.editForm.pics.splice(i, 1);
            } else {
                // 如果是已经上传，但为保存到服务器中的图片
                // 获取将要删除的图片的临时路径
                const filePath = file.response.data.tmp_path;
                // 根据临时路径,从 pics 数组中查找到要删除的图片的索引值
                const i = this.editForm.pics.findIndex(item => item.pic === filePath);
                // 调用数组的 splice 方法, 把图片信息对象从 pics 数组中移除
                this.editForm.pics.splice(i, 1);
            }
        },
        // 监听图片上传成功的事件
        handleSuccess(response) {
            // 1. 拼接得到一个图片信息对象
            const picInfo = { pic: response.data.tmp_path };
            // 2. 将图片信息对象, push 到 pics 数组中
            this.editForm.pics.push(picInfo);
        },
        // 编辑商品
        editGood() {
            this.$refs.editFormRef.validate(valid => {
                if (!valid) return this.$message.error({message: '请填写必要的表单项', duration: 1200});
                // 发送请求，编辑商品
                this.editForm.attrs = [];
                // 编辑动态参数
                 this.manyTableData.forEach(item => {
                    this.editForm.attrs.push({attr_id: item.attr_id, attr_value: item.attr_vals.join(',')})
                })
                // 编辑静态属性
                this.onlyTableData.forEach(item => {
                    this.editForm.attrs.push({attr_id: item.attr_id, attr_value: item.attr_vals})
                })
                // 编辑图片
                this.editForm.pics.forEach((item, i) => {
                    if (item.pics_id) {
                        const mid = item.pics_mid;
                        const pic = mid.substring('/uploads/goodspics/mid_'.length);
                        this.editForm.pics[i] = {pic: 'tmp_uploads\\' + pic};
                    }
                })
                // 发起请求
                this.$api.httpPut('goods/' + this.goodId, this.editForm, {showOuter: 'error', success: ['编辑商品成功！'], error: ['编辑商品失败！']}, 200)
                .then(() => {
                    this.$router.push('/home/goods');
                })
            });
        },
    },
    computed: {
        cateId() {
            if (this.goods_cat.length === 3) {
                return this.goods_cat[2];
            }
            return null;
        }
    }
}
</script>

<style lang='less' scoped>
.previewImg {
    width: 100%;
}
.btnEdit {
    margin-top: 15px;
}
</style>