<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
            <el-breadcrumb-item>添加商品</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card>
            <!-- 提示区域 -->
            <el-alert title="添加商品信息" type="info" center show-icon :closable="false" />
            <!-- 进度条区域 -->
            <el-steps :active="activeIndex * 1" finish-status="success" align-center>
                <el-step title="基本信息" />
                <el-step title="商品参数" />
                <el-step title="商品属性" />
                <el-step title="商品图片" />
                <el-step title="商品内容" />
                <el-step title="完成" />
            </el-steps>
            <!-- 标签页区域 -->
            <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="100px" label-position="top">
                <el-tabs tab-position="left" v-model="activeIndex" :before-leave="beforeTabChange" @tab-change="tabChanged">
                    <el-tab-pane label="基本信息" name="0">
                        <el-form-item label="商品名称" prop="goods_name">
                            <el-input v-model="addForm.goods_name" placeholder="请输入商品名称" />
                        </el-form-item>
                        <el-form-item label="商品价格" prop="goods_price">
                            <el-input v-model="addForm.goods_price" type="number" placeholder="请输入商品价格" />
                        </el-form-item>
                        <el-form-item label="商品重量" prop="goods_weight">
                            <el-input v-model="addForm.goods_weight" type="number" placeholder="请输入商品重量" />
                        </el-form-item>
                        <el-form-item label="商品数量" prop="goods_number">
                            <el-input v-model="addForm.goods_number" type="number" placeholder="请输入商品数量" />
                        </el-form-item>
                        <el-form-item label="商品分类" prop="goods_cat">
                            <el-cascader v-model="addForm.goods_cat" :options="cateList" :props="cascadeProps"
                                @change="handleCascadeChange" />
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品参数" name="1">
                        <!-- 渲染商品参数的各项数据 -->
                        <el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox v-for="(cb, i) in item.attr_vals" :key="i" :label="cb" border />
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品属性" name="2">
                        <el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
                            <el-input v-model="item.attr_vals"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品图片" name="3">
                        <el-form-item>
                            <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove"
                                list-type="picture" :headers="headerObj" :on-success="handleSuccess">
                                <el-button type="primary" size="small">点击上传</el-button>
                            </el-upload>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品内容" name="4">
                        <!-- 富文本编辑器组件 -->
                        <quill-editor v-model:content="addForm.goods_introduce" toolbar="full" content-type="html" @textChange="textChange"></quill-editor>
                        <!-- 添加商品的按钮 -->
                        <el-button type="primary" class="btnAdd" @click="addGood">添加商品</el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <!-- 预览图片的对话框 -->
        <el-dialog v-model="previewDialogVisible" title="图片预览" width="80%">
            <img :src="previewPath" alt="预览图片" class="previewImg">
        </el-dialog>
    </div>
</template>

<script>
import { ArrowRight, Search, Edit, Delete, Setting } from '@element-plus/icons-vue'
import {cloneDeep} from 'lodash'
export default {
    data() {
        const numValidate = (rule, value, callback) => {
            if (value < 0) {
                callback(new Error('输入的数字不能小于0'));
            }
            callback();
        }
        return {
            ArrowRight, Search, Edit, Delete,
            // 步骤条的进度管理
            activeIndex: '0',
            // 添加商品的表单数据对象
            addForm: {
                goods_name: '',
                goods_price: 0,
                goods_weight: 0,
                goods_number: 0,
                // 商品分类数组
                goods_cat: [],
                // 已上传图片的地址数组
                pics: [],
                // 商品的详情描述
                goods_introduce: '',
                // 动态参数和静态属性的数组
                attrs: [],
            },
            // 添加商品的表单校验对象
            addFormRules: {
                goods_name: [
                    { required: true, message: '请输入商品名称', trigger: 'blur' },
                ],
                goods_price: [
                    { required: true, message: '请输入商品价格', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
                goods_weight: [
                    { required: true, message: '请输入商品重量', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
                goods_number: [
                    { required: true, message: '请输入商品数量', trigger: 'blur' },
                    { validator: numValidate, trigger: 'blur' },
                ],
                goods_cat: [
                    { required: true, message: '请选择商品分类', trigger: 'blur' },
                ],
            },
            // 商品分类列表
            cateList: [],
            // 级联选择器的配置对象
            cascadeProps: {
                expandTrigger: 'hover',
                label: 'cat_name',
                value: 'cat_id',
                children: 'children',
            },
            // 动态参数列表数据
            manyTableData: [],
            // 静态属性列表数据
            onlyTableData: [],
            // 上传图片的路径
            uploadURL: "http://127.0.0.1:8888/api/private/v1/upload",
            // 上传图片的请求头
            headerObj: {
                Authorization: window.sessionStorage.getItem('token')
            },
            // 将要预览的图片的url
            previewPath: '',
            // 控制预览图片对话框的显示与隐藏
            previewDialogVisible: false,
        }
    },
    created() {
        this.getCateList();
    },
    methods: {
        // 获取所有的商品分类列表
        getCateList() {
            this.$api.httpGet('categories', { showOuter: 'error', error: ['获取商品分类信息失败！'] })
                .then(res => {
                    this.cateList = res.data;
                })
        },
        // 级联选择器选中项目变化时，触发。
        handleCascadeChange() {
            if (this.addForm.goods_cat.length !== 3) {
                this.addForm.goods_cat = [];
            }
            // 选定商品分类后，即时获取分类的一下动态参数和静态属性
            this.getManyTableData();
            this.getOnlyTableData();
        },
        // 发生标签页切换时，在标签页切换之前触发
        beforeTabChange(activeName, oldActiveName) {
            if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
                this.$message.error('请先选择商品分类');
                return false;
            }
        },
        // 标签页被切换后触发
        tabChanged() {
            // 当访问动态参数面板时
            if (this.activeIndex === '1') {
                this.getManyTableData();
            } else if (this.activeIndex === '2') {
                // 当访问静态属性面板时
                this.getOnlyTableData();
            }
        },
        // 获取动态参数
        getManyTableData() {
            this.$api.httpGet(`categories/${this.cateId}/attributes`, { showOuter: 'error', error: ['获取分类参数失败！'] }, 200, { params: { sel: 'many' } })
            .then(res => {
                res.data.forEach(item => {
                    item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ');
                })
                this.manyTableData = res.data;
            })
        },
        // 获取静态属性
        getOnlyTableData() {
            this.$api.httpGet(`categories/${this.cateId}/attributes`, { showOuter: 'error', error: ['获取分类属性失败！'] }, 200, { params: { sel: 'only' } })
            .then(res => {
                this.onlyTableData = res.data;
            })
        },
        // 处理图片预览效果
        handlePreview(file) {
            this.previewPath = file.response.data.url;
            this.previewDialogVisible = true;
        },
        // 处理移除图片的操作
        handleRemove(file) {
            // 获取将要删除的图片的临时路径
            const filePath = file.response.data.tmp_path;
            // 根据临时路径,从 pics 数组中查找到要删除的图片的索引值
            const i = this.addForm.pics.findIndex(item => item.pic === filePath);
            // 调用数组的 splice 方法, 把图片信息对象从 pics 数组中移除
            this.addForm.pics.splice(i, 1);
        },
        // 监听图片上传成功的事件
        handleSuccess(response) {
            // 1. 拼接得到一个图片信息对象
            const picInfo = { pic: response.data.tmp_path };
            // 2. 将图片信息对象, push 到 pics 数组中
            this.addForm.pics.push(picInfo);
        },
        // 添加商品
        addGood() {
            this.$refs.addFormRef.validate(valid => {
                if (!valid) return this.$message.error({message: '请填写必要的表单项', duration: 1200});
                // 发送请求，添加商品
                // 添加动态参数
                 this.manyTableData.forEach(item => {
                    this.addForm.attrs.push({attr_id: item.attr_id, attr_value: item.attr_vals.join(' ')})
                })
                // 添加静态属性
                this.onlyTableData.forEach(item => {
                    this.addForm.attrs.push({attr_id: item.attr_id, attr_value: item.attr_vals})
                })
                // 深拷贝 addForm 对象，防止绑定了addFrom.goods_cat的级联选择器被影响
                const form = cloneDeep(this.addForm);
                // 将 from.goods_cat转化为字符串
                form.goods_cat = form.goods_cat.join(',');
                // 发起请求
                this.$api.httpPost('goods', form, {error: ['创建商品失败！']}, 201)
                .then(() => {
                    this.$router.push('goods');
                })
            });
        },
    },
    computed: {
        cateId() {
            if (this.addForm.goods_cat.length === 3) {
                return this.addForm.goods_cat[2];
            }
            return null;
        }
    }
}
</script>

<style lang='less' scoped>
.previewImg {
    width: 100%;
}
.btnAdd {
    margin-top: 15px;
}
</style>