<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 卡片视图区域 -->
        <el-card>
            <el-row gutter="20">
                <el-col :span="8">
                    <el-input v-model="queryInfo.query" placeholder="请输入内容" clearable @clear="getGoodsList"
                        @keyup.enter="getGoodsList">
                        <template #append>
                            <el-button :icon="Search" @click="getGoodsList" />
                        </template>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="goAddPage">添加商品</el-button>
                </el-col>
            </el-row>
            <!-- 表格区域 -->
            <el-table :data="goodsList" border stripe style="width: 100%">
                <el-table-column v-if="goodsList.length > 0" type="index" label="#"></el-table-column>
                <el-table-column prop="goods_name" label="商品名称" min-width="666px" />
                <el-table-column prop="goods_price" label="商品价格(元)" width="97px" />
                <el-table-column prop="goods_weight" label="商品重量" width="74px" />
                <el-table-column label="创建时间" min-width="150px">
                    <template #default="scope">
                        {{ dateFormat(scope.row.add_time) }}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="165px">
                    <template #default="scope">
                        <el-button type="primary" :icon="Edit" size="small" @click="goEditPage(scope.row.goods_id)">编辑</el-button>
                        <el-button type="danger" :icon="Delete" size="small"
                            @click="removeGood(scope.row.goods_id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页区域 -->
            <el-pagination v-model:current-page="queryInfo.pagenum" v-model:page-size="queryInfo.pagesize" :total="total"
                :page-sizes="[10, 20, 50, 100]" small background layout="total, sizes, prev, pager, next, jumper"
                @size-change="handleSizeChange" @current-change="handleCurrentChange" />
        </el-card>
    </div>
</template>

<script>
import { ArrowRight, Search, Edit, Delete, Setting } from '@element-plus/icons-vue'
import { ssrContextKey } from 'vue';
export default {
    data() {
        return {
            ArrowRight, Search, Edit, Delete,
            // 商品列表
            goodsList: [],
            // 查询参数对象
            queryInfo: {
                query: '',
                // 页面大小
                pagesize: 10,
                // 当前页码
                pagenum: 1
            },
            // 总数据条数
            total: 0,
        }
    },
    created() {
        // 获取商品列表
        this.getGoodsList();
    },
    methods: {
        // 获取商品列表
        getGoodsList() {
            this.$api.httpGet('goods', { showOuter: 'error', error: ['获取商品列表信息失败！'] }, 200, { params: this.queryInfo })
                .then(res => {
                    this.goodsList = res.data.goods;
                    this.total = res.data.total;
                })
        },
        // 处理时间戳：时间戳 -> yyyy-mm-dd hh:mm:ss
        dateFormat(timestamp) {
            const date = new Date(timestamp);

            const y = date.getFullYear();
            const m = (date.getMonth() + 1 + '').padStart(2, '0');
            const d = (date.getDate() + '').padStart(2, '0');

            const hh = (date.getHours() + '').padStart(2, '0');
            const mm = (date.getMinutes() + '').padStart(2, '0');
            const ss = (date.getSeconds() + '').padStart(2, '0');

            return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
        },
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize;
            this.getGoodsList();
        },
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage;
            this.getGoodsList();
        },
        // 点击删除按钮，删除商品
        removeGood(id) {
            this.$confirm(
                '此操作将永久删除该商品，是否继续？',
                '删除',
                {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消',
                    type: 'warning',
                }
            ).then(() => {
                // 点击确认按钮，发送删除请求
                this.$api.httpDelete('goods/' + id, {error: ['删除失败！']})
                .then(() => {
                    // 成功删除商品，刷新页面
                    this.getGoodsList();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '删除取消',
                    duration: 1200,
                })
            })
        },
        // 跳转到添加商品的页面
        goAddPage() {
            // 由此可知，路由push的基本做法就是：
            // 当push一个相对路径时，将当前路径的最后一段去掉，然后用push的参数代替，以拼接出新的路径
            this.$router.push('/home/goods/add');
        },
        // 跳转到编辑商品的页面
        goEditPage(id) {
            window.sessionStorage.setItem('goodId', id);
            this.$router.push('/home/goods/edit');
        }
    }
}
</script>

<style lang="less" scoped></style>
